'use client'
import React, { useState } from 'react'
import { useSocket } from '../../custom-hooks/useSocket'

export const SlotGame: React.FC = () => {
    // Use a mock user ID - in real app, this would come from authentication
    const userId = 'user_123'
    const { gameState, spinResult, isConnected, spin } = useSocket(userId)

    // Local state for bet amount
    const [betAmount, setBetAmount] = useState<number>(10)

    // Handle spin action
    const handleSpin = () => {
        if (isConnected && gameState) {
            // Validate bet amount
            if (betAmount < gameState.minBet || betAmount > gameState.maxBet) {
                alert(
                    `Bet must be between ${gameState.minBet} and ${gameState.maxBet}`
                )
                return
            }

            spin(betAmount)
        }
    }

    return (
        <div className="max-w-md mx-auto p-6 bg-gray-800 rounded-lg shadow-lg">
            <h2 className="text-2xl font-bold text-white mb-4">Slot Machine</h2>

            {/* Connection Status */}
            <div className="mb-4">
                <p
                    className={`
          text-sm font-medium 
          ${isConnected ? 'text-green-500' : 'text-red-500'}
        `}
                >
                    {isConnected ? 'Connected' : 'Disconnected'}
                </p>
            </div>

            {/* Game State */}
            {gameState && (
                <div className="mb-4 text-white">
                    <p>Balance: ${gameState.balance}</p>
                </div>
            )}

            {/* Reels Display */}
            <div className="flex justify-center mb-4 text-4xl">
                {spinResult?.reels ? (
                    spinResult.reels.map((symbol, index) => (
                        <div key={index} className="mx-2">
                            {symbol}
                        </div>
                    ))
                ) : (
                    <div className="text-gray-500">Spin to play!</div>
                )}
            </div>

            {/* Spin Result */}
            {spinResult && (
                <div
                    className={`
          mb-4 p-2 rounded 
          ${spinResult.isWin ? 'bg-green-200' : 'bg-red-200'}
        `}
                >
                    {spinResult.isWin
                        ? `You won $${spinResult.winnings}!`
                        : `You lost $${Math.abs(spinResult.winnings || 0)}`}
                </div>
            )}

            {/* Bet Control */}
            <div className="flex items-center mb-4">
                <label className="mr-2 text-white">Bet Amount:</label>
                <input
                    type="number"
                    value={betAmount}
                    onChange={(e) => setBetAmount(Number(e.target.value))}
                    min={gameState?.minBet}
                    max={gameState?.maxBet}
                    className="w-20 p-2 rounded bg-gray-700 text-white"
                />
            </div>

            {/* Spin Button */}
            <button
                onClick={handleSpin}
                disabled={!isConnected}
                className={`
          w-full p-3 rounded 
          ${
              isConnected
                  ? 'bg-blue-500 hover:bg-blue-600 text-white'
                  : 'bg-gray-500 text-gray-300 cursor-not-allowed'
          }
        `}
            >
                Spin
            </button>
        </div>
    )
}

export default SlotGame
